  margin-top: 10%;
}

.slider {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: relative;
}

.slider-image-container {
  width: 80%;
  height: auto;
  display: flex;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.slider__image {
  width: 100%;
  object-fit: cover;
  position: relative;
}

.image-overlay {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 10;
}

.image-overlay__content {
  font-size: 3rem;
  font-weight: 700;
  color: $light-color;
}

.image-overlay--like {
  background: $success-color;
}

.image-overlay--dislike {
  background: $danger-color;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.25s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0.2;
}
</style>
